'use client';

import React, { useEffect, useState, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import { useAuthStore, useDeviceStore } from '@/lib/store';
import { toast } from 'sonner';
import { apiClient } from '@/lib/api';
import Layout from '@/components/layout/Layout';
import StatsCard from '@/components/dashboard/StatsCard';
import DeviceCard from '@/components/dashboard/DeviceCard';
import SceneCard from '@/components/dashboard/SceneCard';
import { Card } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { 
  Wifi, 
  WifiOff, 
  RefreshCw,
  Zap,
  Users,
  Activity
} from 'lucide-react';
import { Device } from '@/types';

const DashboardPage = () => {
  const router = useRouter();
  const { user, isAuthenticated, logout } = useAuthStore();
  const { 
    devices, 
    loading, 
    error, 
    fetchDevices, 
    updateDevice,
    setError
  } = useDeviceStore();

  // State for homes and scenes
  const [scenes, setScenes] = useState<Array<{id: string; name: string; description?: string; enabled: boolean; homeId: string}>>([]);
  const [sharedDevices, setSharedDevices] = useState<Device[]>([]);

  const fetchScenes = useCallback(async (homeId: string) => {
    try {
      const response = await apiClient.getScenes(homeId);
      if (response.success && response.data) {
        setScenes(response.data);
      }
    } catch (err) {
      console.error('Failed to fetch scenes:', err);
    }
  }, []);

  const fetchSharedDevices = useCallback(async () => {
    try {
      const response = await apiClient.getDevices();
      if (response.success && response.data) {
        // Lc thit b c chia s (c th da vo thuc tnh shared hoc owner)
        const shared = response.data.filter(device => 
          device.name.includes('Shared') || 
          device.name.includes('Chia s') ||
          device.model?.includes('shared')
        );
        setSharedDevices(shared);
      }
    } catch (err) {
      console.error('Failed to fetch shared devices:', err);
    }
  }, []);

  const fetchHomes = useCallback(async () => {
    try {
      const response = await apiClient.getHomes();
      if (response.success && response.data && response.data.length > 0) {
        const homeId = response.data[0].id;
        await fetchScenes(homeId);
      }
    } catch (err) {
      console.error('Failed to fetch homes:', err);
    }
  }, [fetchScenes]);

  useEffect(() => {
    if (!isAuthenticated) {
      router.push('/login');
      return;
    }

    // Khi phc session Mijia nu c
    if (user && user.mijiaSession) {
      // Session  c set trong store khi login
    }
    
    fetchDevices();
    fetchHomes();
    fetchSharedDevices();
  }, [isAuthenticated, router, fetchDevices, fetchHomes, fetchSharedDevices, user]);


  const handleDeviceToggle = async (deviceId: string, state: boolean) => {
    try {
      await updateDevice(deviceId, { power: state });
      toast.success(`Thit b  c ${state ? 'bt' : 'tt'}`);
    } catch (err) {
      console.error('Error toggling device:', err);
      toast.error('Khng th iu khin thit b');
    }
  };

  const handleDeviceControl = async (deviceId: string, properties: Record<string, unknown>) => {
    try {
      await updateDevice(deviceId, properties);
      toast.success('Thit b  c cp nht');
    } catch (err) {
      console.error('Error controlling device:', err);
      toast.error('Khng th cp nht thit b');
    }
  };

  const handleRunScene = async (sceneId: string) => {
    try {
      const response = await apiClient.runScene(sceneId);
      if (response.success) {
        toast.success('Kch bn  c thc thi');
      } else {
        toast.error('Li khi thc thi kch bn');
      }
    } catch (err) {
      console.error('Error running scene:', err);
      toast.error('Li khi thc thi kch bn');
    }
  };

  const handleRefresh = () => {
    setError(null);
    fetchDevices();
    fetchHomes();
    fetchSharedDevices();
  };

  if (loading) {
    return (
      <Layout>
        <div className="flex items-center justify-center h-96">
          <div className="text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
            <p className="mt-4 text-slate-600 font-medium">ang ti d liu...</p>
          </div>
        </div>
      </Layout>
    );
  }

  const onlineDevices = devices.filter(d => d.online).length;
  const totalScenes = scenes.length;
  const activeScenes = scenes.filter(s => s.enabled).length;

  return (
    <Layout>
      {/* Page Header */}
      <div className="mb-8 relative">
        <div className="absolute inset-0 bg-gradient-to-r from-pink-500/10 via-purple-500/10 to-blue-500/10 rounded-3xl blur-xl"></div>
        <div className="relative bg-gradient-to-r from-pink-500/20 via-purple-500/20 to-blue-500/20 backdrop-blur-sm rounded-3xl p-8 border border-white/20">
          <div className="flex items-center justify-between">
            <div className="animate-fade-in">
              <h1 className="text-5xl font-bold gradient-text mb-2"> Dashboard</h1>
              <p className="text-slate-700 text-xl"> Qun l thit b thng minh ca bn </p>
            </div>
            <div className="flex items-center space-x-3">
              <Button 
                variant="outline" 
                size="lg"
                onClick={handleRefresh}
                disabled={loading}
                className="bg-gradient-to-r from-pink-500 to-purple-500 hover:from-pink-600 hover:to-purple-600 text-white border-0 shadow-lg hover:shadow-xl transform hover:scale-105 transition-all duration-300"
              >
                <RefreshCw className={`h-5 w-5 mr-2 ${loading ? 'animate-spin' : ''}`} />
                 Lm mi
              </Button>
            </div>
          </div>
        </div>
      </div>

      {/* Error Alert */}
      {error && (
        <Alert className="mb-6" variant="destructive">
          <AlertDescription className="flex items-center justify-between">
            <span>{error}</span>
            <Button variant="outline" size="sm" onClick={() => setError(null)}>
              ng
            </Button>
          </AlertDescription>
        </Alert>
      )}

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
        <StatsCard
          title="Tng thit b"
          value={devices.length}
          description="Thit b c kt ni"
          icon={Wifi}
          trend={{ value: 12, label: "so vi thng trc" }}
          status="info"
        />
        <StatsCard
          title="ang hot ng"
          value={onlineDevices}
          description="Thit b trc tuyn"
          icon={Activity}
          trend={{ value: 8, label: "tng t hm qua" }}
          status="success"
        />
        <StatsCard
          title="Kch bn"
          value={totalScenes}
          description="Kch bn t ng"
          icon={Zap}
          trend={{ value: 3, label: "kch bn mi" }}
          status="warning"
        />
        <StatsCard
          title="Thit b chia s"
          value={sharedDevices.length}
          description="c chia s vi ngi khc"
          icon={Users}
          status="info"
        />
      </div>

      {/* Main Content Tabs */}
      <Tabs defaultValue="devices" className="space-y-6">
        <TabsList className="grid w-full grid-cols-3">
          <TabsTrigger value="devices">Thit b</TabsTrigger>
          <TabsTrigger value="scenes">Kch bn</TabsTrigger>
          <TabsTrigger value="shared">Chia s</TabsTrigger>
        </TabsList>

        {/* Devices Tab */}
        <TabsContent value="devices" className="space-y-6">
          <div className="flex items-center justify-between">
            <h2 className="text-xl font-semibold text-slate-900">Thit b ca bn</h2>
            <Badge variant="outline" className="text-sm">
              {onlineDevices} / {devices.length} ang hot ng
            </Badge>
          </div>

          {devices.length === 0 ? (
            <Card className="p-12 text-center">
              <WifiOff className="h-12 w-12 text-slate-400 mx-auto mb-4" />
              <h3 className="text-lg font-medium text-slate-900 mb-2">Cha c thit b no</h3>
              <p className="text-slate-500 mb-4">Hy thm thit b u tin ca bn</p>
              <Button onClick={handleRefresh}>
                <RefreshCw className="h-4 w-4 mr-2" />
                Ti li
              </Button>
            </Card>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
              {devices.map((device) => (
                <DeviceCard
                  key={device.id}
                  device={device}
                  onToggle={handleDeviceToggle}
                  onControl={handleDeviceControl}
                />
              ))}
            </div>
          )}
        </TabsContent>

        {/* Scenes Tab */}
        <TabsContent value="scenes" className="space-y-6">
          <div className="flex items-center justify-between">
            <h2 className="text-xl font-semibold text-slate-900">Kch bn t ng</h2>
            <Badge variant="outline" className="text-sm">
              {activeScenes} / {totalScenes} ang hot ng
            </Badge>
          </div>

          {scenes.length === 0 ? (
            <Card className="p-12 text-center">
              <Zap className="h-12 w-12 text-slate-400 mx-auto mb-4" />
              <h3 className="text-lg font-medium text-slate-900 mb-2">Cha c kch bn no</h3>
              <p className="text-slate-500 mb-4">To kch bn u tin  t ng ha ngi nh</p>
              <Button onClick={handleRefresh}>
                <RefreshCw className="h-4 w-4 mr-2" />
                Ti li
              </Button>
            </Card>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {scenes.map((scene) => (
                <SceneCard
                  key={scene.id}
                  scene={scene}
                  onRun={handleRunScene}
                />
              ))}
            </div>
          )}
        </TabsContent>

        {/* Shared Devices Tab */}
        <TabsContent value="shared" className="space-y-6">
          <div className="flex items-center justify-between">
            <h2 className="text-xl font-semibold text-slate-900">Thit b c chia s</h2>
            <Badge variant="outline" className="text-sm">
              {sharedDevices.length} thit b
            </Badge>
          </div>

          {sharedDevices.length === 0 ? (
            <Card className="p-12 text-center">
              <Users className="h-12 w-12 text-slate-400 mx-auto mb-4" />
              <h3 className="text-lg font-medium text-slate-900 mb-2">Cha c thit b chia s</h3>
              <p className="text-slate-500 mb-4">Cc thit b c chia s vi bn s xut hin  y</p>
              <Button onClick={handleRefresh}>
                <RefreshCw className="h-4 w-4 mr-2" />
                Ti li
              </Button>
            </Card>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
              {sharedDevices.map((device) => (
                <DeviceCard
                  key={device.id}
                  device={device}
                  onToggle={handleDeviceToggle}
                  onControl={handleDeviceControl}
                />
              ))}
            </div>
          )}
        </TabsContent>
      </Tabs>
    </Layout>
  );
};

export default DashboardPage;
